import React from 'react';
import Img from '../../assets/img.png'

const MainTitle = () => (
  <div className="main__title">
    <div className="main__title__one">
      <div className="main__title__h">
        <h1>Car Repair <br/> and Maintenance</h1>
        <p><span className="main__span">Lorem ipsum dolor sit, amet</span> consectetur adipisicing elit. Aperiam corrupti aliquid qui assumenda soluta</p>
      </div>
    </div>
    <div className="main__title__two">
      <div className="main__card__img">
        <img src={Img} alt="" />
      </div>
      <button className="main__title__btn">Contact us</button>
    </div>
  </div>
);

export default MainTitle;
