// Slider.js
import React, { useState, useRef, useEffect } from 'react';
import SliderItem from './SliderItem';
import Img1 from '../../assets/img2.png'
import Img2 from '../../assets/img3.png'
import Img3 from '../../assets/img4.png'

const sliderItems = [
 { img: Img1, title: 'General Maintenance', description: 'Lorem ipsum dolor sit, amet consectetur adipisicing elit. Aperiam corrupti aliquid qui assumenda soluta ipsa' },
 { img: Img2, title: 'Repair and Replacement', description: 'Lorem ipsum dolor sit, amet consectetur adipisicing elit. Aperiam corrupti aliquid qui assumenda soluta ipsa' },
 { img: Img3, title: 'Fuel System Repair', description: 'Lorem ipsum dolor sit, amet consectetur adipisicing elit. Aperiam corrupti aliquid qui assumenda soluta ipsa' },
];

const Slider = () => {
 const [currentIndex, setCurrentIndex] = useState(0);
 const sliderRef = useRef(null);

 const nextSlide = () => {
 setCurrentIndex((currentIndex + 1) % sliderItems.length);
 };

 const prevSlide = () => {
 setCurrentIndex((currentIndex - 1 + sliderItems.length) % sliderItems.length);
 };

 useEffect(() => {
 const slider = sliderRef.current;
 const items = Array.from(slider.querySelectorAll('.slider__item'));
 const firstItemClone = items[0].cloneNode(true);
 const lastItemClone = items[items.length - 1].cloneNode(true);

 slider.appendChild(firstItemClone);
 slider.insertBefore(lastItemClone, slider.firstChild);

 const sliderWidth = items[0].offsetWidth * items.length;
 slider.style.width = `${sliderWidth}px`;

 const moveToSlide = (index) => {
   let slideWidth = items[0].offsetWidth;
   let newLeft = index * -slideWidth;

   slider.style.transition = 'transform 0.5s ease-in-out';
   slider.style.transform = `translateX(${newLeft}px)`;
 };

 slider.ontransitionend = () => {
   if (currentIndex <= -1) {
     slider.style.transition = 'none';
     setCurrentIndex(sliderItems.length - 2);
     moveToSlide(sliderItems.length - 2);
   } else if (currentIndex >= sliderItems.length - 1) {
     slider.style.transition = 'none';
     setCurrentIndex(1);
     moveToSlide(1);
   }
 };
 }, []);

 return (
 <div className="slider" ref={sliderRef}>
   {sliderItems.map((item, index) => (
     <SliderItem key={index} img={item.img} title={item.title} description={item.description} />
   ))}
 </div>
 );
};

export default Slider;
