import React from 'react';
// import Logo from './Logo';

const BurgerMenu = ({ toggleNav }) => {
 const [state, setState] = React.useState({
   sp1: false,
   sp2: false,
   sp3: false,
   logo: false
 });

 const handleClick = () => {
   toggleNav();
   setState({
     sp1: !state.sp1,
     sp2: !state.sp2,
     sp3: !state.sp3,
     logo: !state.logo
   });
 };

 const styleSp1 = { backgroundColor: state.sp1 ? 'black' : '' };
 const styleSp2 = { backgroundColor: state.sp2 ? 'black' : '' };
 const styleSp3 = { backgroundColor: state.sp3 ? 'black' : '' };
 const styleLogo = { color: state.logo ? 'black' : '' };

 return (
   <button className="burger" onClick={handleClick}>
     <span className="sp1" style={styleSp1}></span>
     <span className="sp2" style={styleSp2}></span>
     <span className="sp3" style={styleSp3}></span>
     {/* <Logo style={styleLogo} /> */}
   </button>
 );
};

export default BurgerMenu;
